<template>
  <v-container>
    <v-row class="text-center"> </v-row>
    <div class="container">
      <div class="large-12 medium-12 small-12 cell">
        <label
          >Arquivo
          <input type="file" id="file" ref="file" v-on:change="handleFileUpload()" />
        </label>
        <v-btn style="margin: 20px" rounded color="primary" dark v-on:click="submitFile()">
          Enviar Arquivo</v-btn
        >
      </div>
    </div>

    <v-row class="text-center">
      <v-col sm="12">
        <template>
          <v-data-table :headers="headers" :items="itens" class="elevation-1">
            <template v-slot:item.status="{ item }">
              <v-chip :color="getColor(item.status)" dark>{{ item.status }}</v-chip>
            </template>
          </v-data-table>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  /*
      Defines the data used by the component
    */
  data() {
    return {
      file: '',
    };
  },

  methods: {
    /*
        Submits the file to the server
      */
    async submitFile() {
      const formData = new FormData();

      formData.append('file', this.file);
      const res = await axios.post('https://server-cli.herokuapp.com/v1/files/upload', formData, {
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'multipart/form-data',
        },
      });
      console.log(res.data.msg);
    },

    /*
        Handles a change on the file upload
      */
    handleFileUpload() {
      // eslint-disable-next-line prefer-destructuring
      this.file = this.$refs.file.files[0];
      console.log(this.file);
    },
  },
};
</script>
